.categories {
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 56px;
    }
  }
}

.category {
  &__photos {
    @include pageGrid;
    margin-bottom: 16px;
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: $cards-border-radius;
  }

  &__link {
    position: relative;
    border: 2px solid transparent;
    border-radius: $cards-border-radius;
    transition: border $transition-delay;

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }

    &:hover {
      border-color: $main-text-color;
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;

    @include onTablet {
      font-size: 24px;
      line-height: 140%;
    }
  }
}
